import Vue from 'vue'
import Vuex from 'vuex'
import Config from '../config'
import $Tool from '../api/util';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    // data
    currentLanguage: {type:'CN',name:''}
  },
  getters: {
    // 
  },
  mutations: {
    setCurrentLanguage(state,language) {
      Object.assign(state.currentLanguage,language);
      // state.currentLanguage = language;
    },
  },
  actions: {
    //  -- 
  }

});

if (Config.terminalMode !== 'Android') {
  getBowerLanguage();
}else {
  getURLLanguage();
}

//TODO: 
function getURLLanguage() {
  let lv = $Tool.getQueryString('lv');
  console.log('lv',lv);
  if (lv === '0') {
    // 
    store.state.currentLanguage.type = 'EN';
    store.state.currentLanguage.name = 'English';
  }else {
    store.state.currentLanguage.type = 'CN';
    store.state.currentLanguage.name = '';
  }
}

// TODO: 
function getBowerLanguage() {
  let lang = window.navigator.language || window.navigator.userLanguage; //IE
  lang = lang.substr(0, 2); //lang2
  console.log('lang',lang);
  if (lang === 'zh') {
    store.state.currentLanguage.type = 'CN';
    store.state.currentLanguage.name = '';
  } else {
    store.state.currentLanguage.type = 'EN';
    store.state.currentLanguage.name = 'English';
  }
}


export default store;


