import Vue from 'vue'
import VueI18n from 'vue-i18n'
import LangCN from './pack/zh';
import LangEN from './pack/en';
import store from '../store/'

Vue.use(VueI18n);
console.log('currentLanguage.type',store.state.currentLanguage.type);
const i18n = new VueI18n({
  locale: store.state.currentLanguage.type,    // 
  //this.$i18n.locale // locale
  messages: {
    'CN': LangCN,   // 
    'EN': LangEN    // 
  }
});

export default i18n;


